body {
  font-family: "Roboto", sans-serif;
  width: 100%;

  h1 {
    font-weight: 900;
    font-size: 40px;
    color: $color-top-header;
  }

  a {
    transition: all 0.2s ease-in-out;
  }

  img {
    max-width: 100%;
    height: auto;
  }

  .centralized {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .section__btn {
    display: inline-block;
    margin-top: 30px;
    button {
      background-color: $light-blue-color;
      border-color: $light-blue-color;
      padding: 10px 40px;
      border-radius: 3px;
      box-shadow: 0px 3px 6px 2px rgba(15, 109, 169, 0.3);
      font-weight: 600;
      font-size: 15px;
      &:hover {
        background-color: darken($light-blue-color, 4%);
      }
    }
  }
}

/*---Media Queries --*/
@media (min-width: 576px) {
  .banner {
    &-static-content {
      padding-left: 40px !important;
    }
  }
}

@media (min-width: 768px) {
  header {
    .header-menu {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      &__item {
        padding-bottom: 0 !important;
      }
    }
  }

  .banner {
    &-static-content {
      padding-left: 50px !important;
    }
  }

  .contact-info-menu {
    &__menu-items {
      display: flex;
      justify-content: flex-start;
      padding-left: 10px;
    }

    &__menu-item {
      padding-bottom: 0;
    }
  }

  .footer-main {
    &__column {
      padding-bottom: 0 !important;
    }
  }

  .footer-bottom {
    &__social {
      justify-content: flex-end;
    }
  }
}

@media (min-width: 992px) {
  .menu {
    &__row {
      align-items: center;
      flex-direction: row !important;
    }

    &__navbar {
      width: 250px;
    }
    .logo-div {
      padding-bottom: 0 !important;
    }
  }

  .banner {
    &__left-div {
      padding-bottom: 60px !important ;
      padding-right: 35px;
    }

    &-static-content {
      padding-left: 0 !important;
    }
  }

  .section {
    .left-column {
      padding-bottom: 0 !important;
    }
  }

  .contact {
    &__info {
      padding-top: 0 !important;
    }

    &__required {
      display: flex;
      justify-content: space-between;
    }
  }

  .footer-bottom {
    p {
      text-align: right !important;
    }
    &__col {
      padding-bottom: 0 !important;
    }
  }
}

@media (min-width: 1200px) {
  .menu {
    &__row {
      align-items: center;
      &__list {
        &__ul {
          align-items: center;
        }
      }
    }

    &__navbar {
      width: 100%;
      .list__item {
        &__menu {
          top: 100% !important;
          left: 0 !important;
        }
      }
    }

    &__btn {
      &-search {
        margin-left: 30px !important;
        padding-bottom: 0;
      }
    }
  }
}

@media (min-width: 1920px) {
  .container {
    max-width: 1340px;
  }
}
